.conatct-card{
    display: flex;
    align-items: center;
    /* border: 1px solid green; */
    margin: 10px 0;
    background-color: rgb(207, 207, 207);

    
}
@media screen and (max-width: 900px) {
    .conatct-card {
     flex-direction: column;
     background-color: rgb(207, 207, 207);
     
    }
    .conatct-card-logo>img{
        display: none;
        
    }
    .conatct-card-name>p{
        font-size: small;
    }
  }
  


.conatct-card:hover{
    cursor: pointer;
}

.conatct-card-logo{
flex: 0.25;
display: flex;
justify-content: center;
/* border: 1px solid rgb(0, 0, 0); */

}
.conatct-card-logo>img{
    width: 45px;
    border-radius: 80px;
    /* border: 1px solid green; */
}
.conatct-card-name{
flex: 0.75;
}
.conatct-card-name p{
    font-size: 15px;
    font-weight: 600;
}

