.page-sidebar{
    /* border: 1px solid rgb(0, 0, 0); */
    flex: 0.2;
    height: 100vh;
    display: flex;
    flex-direction: column;
    overflow: auto;
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    position: sticky;
    top: 0;
    
    
}
.sidebar-header{
flex: 0.1;
/* border: 1px solid rgb(0, 237, 4); */
display: flex;
justify-content: center;
align-items: center;
}
.sidebar-chats{
    flex: 0.9;
    /* border: 1px solid rgb(13, 0, 115); */
}
.sidebar-header>p{
  font-size: large;
  font-weight: 600;
  
}
.sidebar-more{
  /* border: 1px solid green; */
  height: 30px;
  display: flex;
  align-items: center;
  padding-left: 10px;
  font-size: small;
  font-weight: 500;
}

.loader-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  background: rgb(255, 255, 255);
  z-index: 100;
}

.spinner {
  width: 64px;
  height: 64px;
  border: 8px solid;
  border-color: #3d5af1 transparent #3d5af1 transparent;
  border-radius: 50%;
  animation: spin-anim 1.2s linear infinite;
}

@keyframes spin-anim {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
}


