.page-chatroom{
    flex: 0.8;
    display: flex;
    flex-direction: column;
    overflow: auto;
   
    scrollbar-color: #6969dd ;
  scrollbar-width: thin;
  scrollbar-gutter: stable;
  background: url(https://t4.ftcdn.net/jpg/01/95/42/21/360_F_195422106_cNzNOmCmgf0QbxDVfuOoEc2zEl0gYIL0.jpg);

   
}
.chat-header{
    display: flex;
    width: 80%;
    align-items: center;
    position: fixed;
    top: 0;
    background-color: rgb(254, 254, 254);
    z-index: 100;
    height: 60px;
    
}
.chat-messages{
    flex: 0.90;
    /* height: 100vh; */
    width: 100%;
    margin-top: 65px;
    /* border: 1px solid green; */
    margin-bottom: 60px;

}
#anchor {
    overflow-anchor: auto;
    height: 60px;
    /* border: 1px solid red; */
  }
.chat-input-block{
   height: 60px;
   position: fixed;
   bottom: 0;
   width: 80%;
   background-color: rgb(255, 255, 255);
   display: flex;
   justify-content: center;
   align-items: center;
   /* border: 1px solid red; */

   
}

.chat-input-block>input{
    border-radius: 20px;
    width: 70%;
    height: 34px;
    
}


/* ---------------------------------------------------------------------- */

.chat-button{
    
    margin-left: 10px;
}
.chat-logo{
    flex: 0.07;
    /* border: 1px solid rgb(0, 0, 0);  */
    display: flex;
    justify-content: center;
    margin-left: 10px;
    align-items: center;
}
.chat-logo>img{
    width: 45px;
    border-radius: 40px;
}
.chat-name{
    flex: 0.85;
    /* border: 1px solid rgb(255, 0, 0); */
}
.chat-name>p{
   /* margin-left: 5px; */
   font-size: larger;
   font-weight: 600;

}
.chat__message{
    position: relative;
    font-size: 16px;
    padding: 10px;
    width: fit-content;
    border-radius: 10px;
    background-color: rgb(255, 252, 162);
    margin-bottom: 30px;
    margin-left: 30px;
    color: black;
}
.chat__timestamp{
    margin-left: 10px;
    font-size: xx-small;
}
.chat__name{
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
}
.chat__message__sent{
    margin-left: auto;
    margin-right: 30px;
    background-color: #dcf8c6;
}
.header-back{
    border: 2px solid rgb(0, 0, 0);
    padding: 2px 2px;
    border-radius: 5px;
    font-weight: 500;
}



#scroller * {
    overflow-anchor: none;
   
  }

  .page-chatroom::-webkit-scrollbar {
    width: 12px; 
                  /* width of the entire scrollbar */
  }
  
  .page-chatroom::-webkit-scrollbar-track {
     opacity:0.1;
  }
  .page-chatroom::-webkit-scrollbar-thumb {
    background-color: rgb(60, 57, 57);    /* color of the scroll thumb */
    border-radius: 20px;       /* roundness of the scroll thumb */
     /* creates padding around scroll thumb */
  }
  

  .footer-block{
    flex: 0.1;
  }
  @media screen and (max-width: 900px) {
    .chat-header{
        width: 100%;
    }
    .chat-input-block{
        width: 100%;
    }
    .chat__message{
        margin-left: 10px;
    }
    .chat__message__sent{
        margin-left: auto;
        margin-right: 10px;
    }
    
}
  
